
.list-table-container {
    @extend .table-container;

    display: inline-block;

    td, th {
        border: 1px solid $rgba-complement-1;
    }

    thead tr {
        background-color: $rgba-primary-3;
    }

    tbody tr:nth-child(even) {
      background: $rgba-primary-4;
    }
    tbody tr:nth-child(odd) {
      background: $rgba-complement-4;
    }


    thead .sort-up {
        @include sprite($fleche-haut);
        display: inline-block;
        margin-left: 2px;
    }

    thead .sort-down {
        @include sprite($fleche-bas);
        display: inline-block;
        margin-left: 2px;
    }

    .table-filter {
        margin: 1em 0em 0.5em 0em;
        width: 100%;

    }

    table input[type='text'] {
        width: 100%;
    }

}

